import React, {useState} from "react";
import styled from "styled-components";
import {Button, Typography} from "@mui/material";
import sun from '../../assets/images/lights-sun-svgrepo-com.svg'
import moon from '../../assets/images/crescent-moon-svgrepo-com.svg'
import {changeTemp} from "../../features/tempSlice";
import {useAppDispatch, useAppSelector} from "../../app/hooks";
import {SwitchDarkMode} from "../switch/switch";

export const Header: React.VFC<{ onPageChange: (userPage: string) => void }> = ({onPageChange}) => {
  const dispatch = useAppDispatch()
  const {value} = useAppSelector(state => state.temp)
  const [mode, setMode] = useState('light')

  return (
    <StyledHeader>
      <div className="logo">

        <img src={mode === "light" ? sun : moon} alt="logo"/>
        <StyledTypography variant="h5">
          Weather
        </StyledTypography>
      </div>
      <div className="flex">
        <StyledButton onClick={() => {
          dispatch(changeTemp(value === 'celsius' ? 'fahrenheit' : 'celsius'))
        }}>{value}</StyledButton>
        <SwitchDarkMode/>
      </div>
      <div className="buttons">
        <StyledButton variant="outlined" onClick={() => onPageChange('home')}>Home</StyledButton>
        <StyledButton variant="outlined" onClick={() => onPageChange('favourites')}>Favourites</StyledButton>
      </div>

    </StyledHeader>
  );
};

const StyledHeader = styled.div`
  background-color: #aa93e8;
  display: flex;
  justify-content: space-between;
  padding: 2%;
  align-items: center;

  .flex {
    display: flex;
  }

  .logo {
    max-width: 70px;
    display: flex;
    align-items: center;
  }

  img {
    width: 100%;
    height: auto;
    padding-right: 3px;
    transition: filter 0.5s linear;

    &:hover {
      cursor: pointer;
      filter: invert(1);
    }
  }

`;
const StyledButton = styled(Button)`
  && {
    color: #2e267b;
    border: 1px solid #2e267b;
    margin-right: 3px;
  }
`;
const StyledTypography = styled(Typography)`
  && {
    margin: 0;
  }
`;